import { Component, OnInit, OnChanges, Input, Output, EventEmitter } from '@angular/core';

import {Subject} from 'rxjs/Rx';

import { Ingredient } from '../shared/ingredient';

import { ShoppingListService } from './shopping-list.service';
import { SharedService } from '../shared/shared.service';

@Component({
  selector: 'rb-shopping-list-add',
  template: require('./shopping-list-add.component.html')
})
export class ShoppingListAddComponent implements OnInit, OnChanges {

  isAdd: boolean = true;
  @Input() item: Ingredient;
  @Output() cleared = new EventEmitter();

  firstRightClick;
  links;

  constructor(private sls: ShoppingListService, private sharedService: SharedService) {
//    console.log(this.sharedService.isVegetarian);
    this.links = [
      {title:'a',subject:new Subject()},
      {title:'b', isEnabled: false,subject:new Subject()},
      {title:'csdafasdfasdfasdfasdfasdfsdfasdfsdfasdfsdfasf', isEnabled: true,subject:new Subject()}
    ];
  }

  ngOnInit() {
    this.links.forEach(l => l.subject.subscribe(val=> this.firstCallback(val)));
  }

  ngOnChanges(changes) {
//    console.log(changes.item);
    if (changes.item.currentValue === null) {
      this.isAdd = true;
      this.item = {name: null, amount: null};
    } else {
      this.isAdd = false;
    }
  }

  onSubmit(ingredient: Ingredient) {
    const newItem = new Ingredient(ingredient.name, ingredient.amount);
    if (!this.isAdd) {
      this.sls.editItem(this.item, newItem);
      this.onClear();
    } else {
      this.item = newItem;
      this.sls.addItem(this.item);
    }
  }

  onDelete(){
    this.sls.deleteItem(this.item);
    this.onClear();
  }

  onClear(){
    this.isAdd = true;
    this.cleared.emit(null);
  }

  firstCallback(val){
    this.firstRightClick = val;
  }

}



